.survey-response {
  /* padding: 16px; */
  justify-content: stretch !important;
  text-align: start !important;
}

.survey-response label {
  display: block;
  width: 100%;
  padding: 15px 30px 15px 10px;
  /* font-size: 20px; */
  background-color: #454955;

  cursor: pointer;  
  border: 1px solid #454955;
  border-radius: 10px;
  margin: 0;
}

@media (hover: hover) {
  .survey-response label:hover {
    /* background-color: #454955; */
    background-image: linear-gradient(92.83deg, #0C7DBF 0, #58b0e0 100%);
    border-radius: 10px;
  }
}

.survey-response label:active {
  /* background-color: #0C7DBF; */
  background-image: linear-gradient(0deg, rgba(0, 0, 0, .2), rgba(0, 0, 0, .2)), linear-gradient(92.83deg, #0C7DBF 0, #58b0e0 100%);
  border: 1px solid #0C7DBF;

  /* border-radius: 10px; */
}

.survey-response.selected label {
  background-color: #0C7DBF;
  border: 1px solid #76cdff;

  font-weight: bold;
}

.survey-response input[type=checkbox] {
  margin-right: 16px;
  height: 15px;
  width: 15px;
}

.confirm-button {
  justify-content: end;
}

.confirm-button button {
  width: auto;
}